import { mapGetters, mapActions } from 'vuex'
import TokenServ from '@/api/Token'
export default {
  data () {
    return {
      showSignin: false
    }
  },
  computed: {
    ...mapGetters({loginUser: 'loginUser'}),
    isLogin () { // 
      return this.loginUser.name
    }
  },
  methods: {
    ...mapActions({signout: 'signout', getLoginUser: 'getLoginUser'}) //  this.signin()  this.$store.dispatch('signin')
  },
  mounted () { // token
    const token = TokenServ.getItem()
    if (token) this.getLoginUser(token)
  }
}



